import { useState, useEffect } from "react";
import styles from "styles/InputBlock.module.scss"


//InputBlock heightinputHeight
//(50) needErrorMessage="true"
  //maxLength()
export default function InputBlock({ label, placeholder, value, type, onChange, maxLength, setIsError ,inputHeight, needErrorMessage }) {
  const [errorMessage, setErrorMessage] = useState("")
  
  //accounterrorMessage
  const checkAccount = () => {
    const accountRegex = /^[a-zA-Z0-9]+$/;

    if (value.length > 50) {
      setErrorMessage("50")
    }else if (value.length > 0 && !accountRegex.test(value)) {
      setErrorMessage("")
    }else {
      setErrorMessage("");
    }
  }

  //password
  const checkPassword = () => {
    if (value.length > 20) {
      setErrorMessage("20")
    }else if (value.includes(" ")) {
      setErrorMessage("")
    }else{
      setErrorMessage("");
    }
  }

  //name
  const checkName = () => {
    const whitespaceRegex = /^\s*$/;

    if (value.length > 50) {
      setErrorMessage("50")
    }else if (whitespaceRegex.test(value) && value.length !== 0) {
      setErrorMessage("")
    }else {
      setErrorMessage("");
    }
  }

  //description
  const checkDescription = () => {
    const whitespaceRegex = /^\s*$/;

    if (value.length > 160) {
      setErrorMessage("")
    }else if (whitespaceRegex.test(value) && value.length !== 0) {
      setErrorMessage("")
    }else {
      setErrorMessage("");
    }
  }

  //email
  const checkEmail = () => {
    const whitespaceRegex = /^\s*$/;

    if (value.length > 100) {
      setErrorMessage("")
    }else if (whitespaceRegex.test(value) && value.length !== 0) {
      setErrorMessage("")
    }else {
      setErrorMessage("");
    }
  }

  useEffect(() => {
    if (label === "") {
      checkAccount();
    }else if (label === "" || label === "") {
      checkPassword();
    }else if (label === "") {
      checkName()
    }else if (label === "") {
      checkDescription()
    }else if (label === "Email") {
      checkEmail()
    }
  }, [value, label]);


  //errorMessagesetIsErrortrue const [isError, setIsError] = useState(false)
  //isError === true
  useEffect(() => {
    if (errorMessage !== "") {
      setIsError(true)
    } else {
      setIsError(false)
    }
  }, [errorMessage])


  return (
    <div>
      <div className={styles.inputBlockContainer}>
        <label className={styles.inputLabel} htmlFor={label}>{label}</label>
        <input className={`${styles.inputBlock} ${errorMessage !== "" && needErrorMessage === "true" ? styles.danger : ""}`}
          type={type || 'text'} 
          placeholder={placeholder || ''} 
          value={value || ''}
          onChange={(e) => onChange?.(e.target.value)}
          id={label}
          style={{ height: inputHeight }}
        />
      </div>
      <div className={`${styles.message} small-text-medium`}>
        <div className={`${styles.leftMessage} ${errorMessage !== "" ? styles.danger : ""}`}>
          { needErrorMessage && errorMessage }
        </div>
        <div className={`${styles.rightMessage} ${errorMessage !== "" ? styles.danger : ""}`}>
          { needErrorMessage && value.length}{ needErrorMessage && "/" }{ needErrorMessage && maxLength}
        </div>
      </div>
    </div>
  );
}