import { useState, useEffect } from "react";
import styles from "styles/UserInputModal.module.scss";

export default function InputBlock({
  label,
  placeholder,
  value,
  onChange,
  maxLength,
  setIsError,
  textareaHeight,
}) {
  const [errorMessage, setErrorMessage] = useState("");

  //  name  errorMessage
  const checkName = () => {
    if (value.length > maxLength) {
      setErrorMessage(`${maxLength}`);
    } else {
      setErrorMessage("");
    }
  };

  //  description 
  const checkDescription = () => {
    if (value.length > maxLength) {
      setErrorMessage(`${maxLength}`);
    } else if (value.trim() === " ") {
      setErrorMessage("");
    } else {
      setErrorMessage("");
    }
  };

  useEffect(() => {
    if (label === "") {
      checkName();
    } else if (label === "") {
      checkDescription();
    }
  }, [value, label]);

  useEffect(() => {
    if (errorMessage !== "") {
      setIsError(true);
    } else {
      setIsError(false);
    }
  }, [errorMessage]);

  return (
    <div>
      <div className={styles.inputBlockContainer}>
        <label className={styles.inputLabel} htmlFor={label}>
          {label}
        </label>
        <textarea
          className={`${styles.inputBlock} ${
            errorMessage !== "" ? styles.danger : ""
          }`}
          type="text"
          placeholder={placeholder || ""}
          value={value || ""}
          onChange={(e) => onChange?.(e.target.value)}
          id={label}
          style={{ height: textareaHeight }}
        />
      </div>
      <div className={`${styles.message} small-text-medium`}>
        <div
          className={`${styles.leftMessage} ${
            errorMessage !== "" ? styles.danger : ""
          }`}
        >
          {errorMessage}
        </div>
        <div
          className={`${styles.rightMessage} ${
            errorMessage !== "" ? styles.danger : ""
          }`}
        >
          {value.length}/{maxLength}
        </div>
      </div>
    </div>
  );
}
